					appvue,vuex,vue-router,vue-kookie,php,mysql,
					jquery,webpack.......
					app<br />
					~~
					
					
				</p>
				<p @click="backSetting()" class="backSetting"></p>
			</div>
			<!---->
			<div class="fkForme" v-if="fkforme">
