	export default {
		data() {
				return {
					src: require("../../img/newsLogo.jpg"),
					smileImg: require("../../img/smile.png"),
					setting:"",
					releaseProfile: true,
					update: false,
					showIntrodce:false,
					aboutApp:false,
					fkforme:false,
					greeHe:false
				}
			},
			computed: {

			},
			methods: {
				//
				eUpdate() {
					this.releaseProfile = false;
					this.update = true;
					this.setting="";
				},
				//
				introduce(){
					this.releaseProfile = false;
					this.showIntrodce= true;
					this.setting = "";
				},
				aboutEnewsApp(){
					this.releaseProfile = false;
					this.aboutApp = true;
					this.setting = "APP"
				},
				//
				fkForme(){
					this.releaseProfile = false;
					this.fkforme = true;
					this.setting = "";
				},
				//
				EGree(){
					this.releaseProfile = false;
					this.greeHe = true;
					this.setting = "";
				},
				//
				backSetting(){
					this.update = false
					this.releaseProfile = true;
					this.showIntrodce = false;
					this.aboutApp = false;
					this.fkforme = false;
					this.greeHe = false;
					this.setting = "";
				}
			},
			mounted() {

			}
	}
</script>
