			top: 0;
			text-align: center;
			font-size: 20px;
			line-height: 48px;
			background-color: #474a4f;
			color: #fff;
			max-width: 450px;left: 0;right: 0;margin: auto;
			.routeReset {
				color: #fff;
			}
			span {
				width: 48px;
				height: 48px;
				display: inline-block;
				z-index: 1;
				position: absolute;
				left: 15px;
				top: 0;
			}
		}
		.eAppLogo {
			width: 180px;
			height: 180px;
			position: absolute;
			margin: auto;
			top: 110px;
