			vertical-align: middle;
		}
		
	}
	.backSetting{text-decoration: underline; color: red;text-align: center;}
	.eIntroduce{
		width: 100%;
		padding: 0 5%;
		p {
			text-align: center;
			font-size: 20px;
		};
	}
	.aboutEnewsApp{
		width: 100%;
		padding: 0 5%;
		p{
			text-align: left;
			font-size: 20px;
			line-height: 2;
		}
	}
	.fkForme{
		width: 100%;
		p{
			font-size: 20px;
			text-align: center;
		}
	}
	.EGree{
		width: 100%;
		p{
			font-size: 20px;
			text-align: center;
		}
	}
</style>


