//import 
import $ from "jquery";
window.$ = $;
//slider
import Swiper from 'swiper'
import 'swiper/dist/css/swiper.min.css';
//import Vue & VueRouter & Vuex
import Vue from "vue";
import VueRouter from "vue-router";
Vue.use(VueRouter);
import Vuex from 'vuex'
Vue.use(Vuex);
var VueCookie = require('vue-cookie');
Vue.use(VueCookie);

//axios
import axios from 'axios'
Vue.prototype.$http = axios  //axios  this.$http
//import  MuseUI
import MuseUI from 'muse-ui'
import 'muse-ui/dist/muse-ui.css'
import 'muse-ui/dist/theme-carbon.css' //  carbon 
Vue.use(MuseUI);

//import index component

import war from "./components/war.vue";
import travel from "./components/travel.vue";
import lady from "./components/lady.vue";
import ent from "./components/ent.vue";
import sport from "./components/sport.vue";


//
var userLogReg = require("./components/userLogReg/userLogReg.vue");
//
var setting = require("./components/setting/setting.vue");
//
var detail = require("./components/detail.vue");
//
var index = require("./components/index.vue");
//   
//var yydetail = require("./components/yydetail/yydetail.vue");

//setting router 
var router = new VueRouter({
	routes: [{
		path: "/index",
		component: index,
		children:[{
			path: "war",
			component: war
		},{
			path: "travel",
			component: travel
		},{
			path: "lady",
			component: lady
		},{
			path: "ent",
			component: ent
		},{
			path: "sport",
			component: sport
		}]
	}
	,{
		path: "/user",
		component: userLogReg
	},
	{
		path: "/setting",
		component: setting
	},
	{
		path: "/detail/:id",
		component: detail
	},{
		path:"/",
		redirect:"/index/travel"
	}]
});
//   vuex 
var store = new Vuex.Store({
		state: {
			type:'travel',
			nickName:'E-Gree'
		},
		mutations: {
			getType(state,type){
				state.type = type;
			},
			getNickName(state,nickName){
				state.nickName = nickName;
			}
		},
		getters: {

		}
	})
	//vue:
new Vue({
	el: "#demo",
//	render: (createElement) => createElement(index),
	template: `
		<router-view></router-view>
	`,
	//
	router,
	store,
	data: {
		author: "Alex"
	}
});


